@mixin syntax_dark {

    /* Background */
    .bg {
        color: #f8f8f2 !important;
        background-color: #272822 !important;
    }

    /* PreWrapper */
    .chroma {
        color: #f8f8f2 !important;
        background-color: #272822 !important;
    }

    /* Other */
    .chroma .x {}

    /* Error */
    .chroma .err {
        color: #960050 !important;
        background-color: #1e0010 !important;
    }

    /* CodeLine */
    .chroma .cl {}

    /* LineLink */
    .chroma .lnlinks {
        outline: none !important;;
        text-decoration: none !important;
        color: inherit !important;
    }

    /* LineTableTD */
    .chroma .lntd {
        vertical-align: top;
        padding: 0 !important;
        margin: 0 !important;
        border: 0 !important;
    }

    /* LineTable */
    .chroma .lntable {
        border-spacing: 0 !important;
        padding: 0 !important;
        margin: 0 !important;
        border: 0 !important;
    }

    /* LineHighlight */
    .chroma .hl {
        background-color: #3c3d38 !important;
    }

    /* LineNumbersTable */
    .chroma .lnt {
        white-space: pre;
        -webkit-user-select: none !important;
        user-select: none !important;
        margin-right: 0.4em !important;
        padding: 0 0.4em 0 0.4em !important;
        color: #7f7f7f !important;
    }

    /* LineNumbers */
    .chroma .ln {
        white-space: pre;
        -webkit-user-select: none !important;
        user-select: none !important;
        margin-right: 0.4em !important;
        padding: 0 0.4em 0 0.4em !important;
        color: #7f7f7f !important;
    }

    /* Line */
    .chroma .line {
        display: flex !important;
    }

    /* Keyword */
    .chroma .k {
        color: #66d9ef !important;
    }

    /* KeywordConstant */
    .chroma .kc {
        color: #66d9ef !important;
    }

    /* KeywordDeclaration */
    .chroma .kd {
        color: #66d9ef !important;
    }

    /* KeywordNamespace */
    .chroma .kn {
        color: #f92672 !important;
    }

    /* KeywordPseudo */
    .chroma .kp {
        color: #66d9ef !important;
    }

    /* KeywordReserved */
    .chroma .kr {
        color: #66d9ef !important;
    }

    /* KeywordType */
    .chroma .kt {
        color: #66d9ef !important;
    }

    /* Name */
    .chroma .n {}

    /* NameAttribute */
    .chroma .na {
        color: #a6e22e !important;
    }

    /* NameBuiltin */
    .chroma .nb {}

    /* NameBuiltinPseudo */
    .chroma .bp {}

    /* NameClass */
    .chroma .nc {
        color: #a6e22e !important;
    }

    /* NameConstant */
    .chroma .no {
        color: #66d9ef !important;
    }

    /* NameDecorator */
    .chroma .nd {
        color: #a6e22e !important;
    }

    /* NameEntity */
    .chroma .ni {}

    /* NameException */
    .chroma .ne {
        color: #a6e22e !important;
    }

    /* NameFunction */
    .chroma .nf {
        color: #a6e22e !important;
    }

    /* NameFunctionMagic */
    .chroma .fm {}

    /* NameLabel */
    .chroma .nl {}

    /* NameNamespace */
    .chroma .nn {}

    /* NameOther */
    .chroma .nx {
        color: #a6e22e !important;
    }

    /* NameProperty */
    .chroma .py {}

    /* NameTag */
    .chroma .nt {
        color: #f92672 !important;
    }

    /* NameVariable */
    .chroma .nv {}

    /* NameVariableClass */
    .chroma .vc {}

    /* NameVariableGlobal */
    .chroma .vg {}

    /* NameVariableInstance */
    .chroma .vi {}

    /* NameVariableMagic */
    .chroma .vm {}

    /* Literal */
    .chroma .l {
        color: #ae81ff !important;
    }

    /* LiteralDate */
    .chroma .ld {
        color: #e6db74 !important;
    }

    /* LiteralString */
    .chroma .s {
        color: #e6db74 !important;
    }

    /* LiteralStringAffix */
    .chroma .sa {
        color: #e6db74 !important;
    }

    /* LiteralStringBacktick */
    .chroma .sb {
        color: #e6db74 !important;
    }

    /* LiteralStringChar */
    .chroma .sc {
        color: #e6db74 !important;
    }

    /* LiteralStringDelimiter */
    .chroma .dl {
        color: #e6db74 !important;
    }

    /* LiteralStringDoc */
    .chroma .sd {
        color: #e6db74 !important;
    }

    /* LiteralStringDouble */
    .chroma .s2 {
        color: #e6db74 !important;
    }

    /* LiteralStringEscape */
    .chroma .se {
        color: #ae81ff !important;
    }

    /* LiteralStringHeredoc */
    .chroma .sh {
        color: #e6db74 !important;
    }

    /* LiteralStringInterpol */
    .chroma .si {
        color: #e6db74 !important;
    }

    /* LiteralStringOther */
    .chroma .sx {
        color: #e6db74 !important;
    }

    /* LiteralStringRegex */
    .chroma .sr {
        color: #e6db74 !important;
    }

    /* LiteralStringSingle */
    .chroma .s1 {
        color: #e6db74 !important;
    }

    /* LiteralStringSymbol */
    .chroma .ss {
        color: #e6db74 !important;
    }

    /* LiteralNumber */
    .chroma .m {
        color: #ae81ff !important;
    }

    /* LiteralNumberBin */
    .chroma .mb {
        color: #ae81ff !important;
    }

    /* LiteralNumberFloat */
    .chroma .mf {
        color: #ae81ff !important;
    }

    /* LiteralNumberHex */
    .chroma .mh {
        color: #ae81ff !important;
    }

    /* LiteralNumberInteger */
    .chroma .mi {
        color: #ae81ff !important;
    }

    /* LiteralNumberIntegerLong */
    .chroma .il {
        color: #ae81ff !important;
    }

    /* LiteralNumberOct */
    .chroma .mo {
        color: #ae81ff !important;
    }

    /* Operator */
    .chroma .o {
        color: #f92672 !important;
    }

    /* OperatorWord */
    .chroma .ow {
        color: #f92672 !important;
    }

    /* Punctuation */
    .chroma .p {}

    /* Comment */
    .chroma .c {
        color: #75715e !important;
    }

    /* CommentHashbang */
    .chroma .ch {
        color: #75715e !important;
    }

    /* CommentMultiline */
    .chroma .cm {
        color: #75715e !important;
    }

    /* CommentSingle */
    .chroma .c1 {
        color: #75715e !important;
    }

    /* CommentSpecial */
    .chroma .cs {
        color: #75715e !important;
    }

    /* CommentPreproc */
    .chroma .cp {
        color: #75715e !important;
    }

    /* CommentPreprocFile */
    .chroma .cpf {
        color: #75715e !important;
    }

    /* Generic */
    .chroma .g {}

    /* GenericDeleted */
    .chroma .gd {
        color: #f92672 !important;
    }

    /* GenericEmph */
    .chroma .ge {
        font-style: italic !important;
    }

    /* GenericError */
    .chroma .gr {}

    /* GenericHeading */
    .chroma .gh {}

    /* GenericInserted */
    .chroma .gi {
        color: #a6e22e !important;
    }

    /* GenericOutput */
    .chroma .go {}

    /* GenericPrompt */
    .chroma .gp {}

    /* GenericStrong */
    .chroma .gs {
        font-weight: bold !important;
    }

    /* GenericSubheading */
    .chroma .gu {
        color: #75715e !important;
    }

    /* GenericTraceback */
    .chroma .gt {}

    /* GenericUnderline */
    .chroma .gl {}

    /* TextWhitespace */
    .chroma .w {}
}

body.colorscheme-dark {
    @include syntax_dark();
}
body.colorscheme-light {
    @include syntax_dark();
}

body.colorscheme-auto {
    @media (prefers-color-scheme: dark) {
        @include syntax_dark();
    }
}