@media (prefers-reduced-motion: no-preference) {
  .about {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .bio-image {
    position: relative
  }

  .bio-image::before {
    display: block;
    content: "";
    height: 90%;
    width: 160%;
    position: absolute;
    background-image: url(data:image/png;base64,);
    background-size: 100%;
    background-repeat: no-repeat;
    top: 89%;
    left: -30%;
    right: 0;
    transform-origin: 30% 50%;
    opacity: 1;
    transition: opacity .3s ease-in-out
  }

  body.colorscheme-dark {
    .bio-image::before {
      filter: invert(100%);
    }
  }

  .bio-image:hover::before {
    opacity: 0
  }

  .bio-image-wrapper {
    transform: scale(0.84) translateX(15%) rotateZ(-11deg);
    transform-origin: 0 100%;
    overflow: hidden;
    transition: transform .2s ease-out;
    border-radius: 70% 30% 40% 60% / 30% 30% 70% 70%;
  }

  .bio-image-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    transform: scale(1.2)rotateZ(11deg);
    transition: transform .2s ease-out
  }

  .bio-image-link:active .bio-image-wrapper,
  .bio-image-link:focus .bio-image-wrapper,
  .bio-image:hover .bio-image-wrapper {
    transform: scale(.84)translateX(15%)rotateZ(0)
  }

  .bio-image-link:active .bio-image-image,
  .bio-image-link:focus .bio-image-image,
  .bio-image:hover .bio-image-image {
    transform: scale(1.2)rotateZ(0)
  }
}