$mobile-small-breakpoint: 320px;
$mobile-medium-breakpoint: 480px;
$tablet-breakpoint: 768px;
$desktop-breakpoint: 1024px;

$card-height-small: 288px;
$card-height-medium: 352px;
$card-height-large: 412px;


.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(301px, 1fr));
  gap: 20px;
}

/* 4K */
@media only screen and (min-width: 1441px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    // background-color: darkturquoise;
  }
}

.project-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid light-dark(#ddd, #333);
  padding: 20px;
  border-radius: 8px;
  background-color: light-dark(#fff, #222);
  min-height: 400px;
}

.card-header {
  text-align: center;
  font-weight: bold;
  margin-bottom: 10px;
  font-size: 2.15rem;
}

.card-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 15px;
  word-wrap: anywhere;

  h3 {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    text-align: center;
    margin: unset;
    margin-top: 10px;
  }

  p {
    margin: unset;
    // text-align: center;
  }


  /* Mobile */
  @media only screen and (max-width: 676px) {
    h3 {
      min-height: unset;
      // background-color: lightseagreen;
    }

    p {
      min-height: unset;
      // background-color: lightseagreen;
    }
  }

  /* Tablet */
  @media only screen and (min-width: 677px) and (max-width: $tablet-breakpoint) {
    h3 {
      min-height: 132px;
      // background-color: #00FF00;
    }

    p {
      min-height: 130px;
      // background-color: #00FF00;
    }
  }

  /* Desktop */
  @media only screen and (min-width: 769px) and (max-width: $desktop-breakpoint) {
    h3 {
      min-height: 114px;
      // background-color: #0000FF;
    }

    p {
      min-height: 164px;
      // background-color: #0000FF;
    }
  }

  /* Wide */
  @media only screen and (min-width: 1000px) and (max-width: 1260px) {
    h3 {
      min-height: 116px;
      // background-color: olivedrab;
    }

    p {
      min-height: 160px;
      // background-color: olivedrab;
    }
  }

  /* 2K */
  @media only screen and (min-width: 1261px) and (max-width: 1440px) {
    h3 {
      min-height: 130px;
      // background-color: gray;
    }

    p {
      min-height: 168px;
      // background-color: gray;
    }
  }

  /* 4K */
  @media only screen and (min-width: 1441px) {
    h3 {
      min-height: 170px;
      // background-color: darkturquoise;
    }

    p {
      min-height: 227px;
      // background-color: darkturquoise;
    }
  }

  &>ul {
    height: 200px;
  }

}

.subsection {
  display: flex;
  flex-direction: column;
  align-items: center;

  span {
    font-size: 2rem;
    font-weight: bold;
  }
}

.card-extra {
  text-align: center;
  margin-bottom: 10px;
}

.project-preview {
  min-height: 250px;
  max-height: 250px;
  display: flex;
  flex: auto;
  aspect-ratio: 1;
  border-radius: 8px;
  object-fit: contain;
  background-color: light-dark(#f4f4f4, #0c0c0c);
}
// 012345678abcdef
// fedcba876543210

.technologies {
  list-style: none;
  padding: 0;
  margin-bottom: 15px;
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
  justify-content: center;
}

.technologies li {
  background-color: light-dark(#f1f1f1, #0e0e0e);
  display: inline-block;
  padding: 5px 10px;
  border-radius: 5px;
}

.project-link {
  display: block;
  text-align: center;
  padding: 10px;
  background-color: light-dark(#007BFF, #007bff3c);
  color: light-dark(#fff, #fbfbfb) !important;
  border-radius: 5px;
  text-decoration: none;
  margin-top: auto;
}